<template>
	<section>
		<h1 style="position: absolute;color: #fff;">three.js学习</h1>
		<div ref="three"></div>
		<div ref="threeLine"></div>
		<u-toast ref="uToast" />
	</section>
</template>

<script>
	import * as Three from "three"
	export default {
		data() {
			return {
				scene: {},
				camera: {},
				renderer: {},
				materials: {},
				materialF: ''
			}
		},
		methods: {
			initThree(materialF, cameraConfig, renderConfig, materialConfig) {
				// 场景
				this.scene[materialF] = new Three.Scene();
				// 渲染器
				this.renderer[materialF] = new Three.WebGLRenderer();
				this.renderer[materialF].setSize(renderConfig.width, renderConfig.height);

				// 透视摄像机--视野角度、长宽比、近截面、远截面
				this.camera[materialF] = new Three.PerspectiveCamera(cameraConfig.angle, cameraConfig.w_to_h, cameraConfig
					.near,
					cameraConfig.far);
				this.camera[materialF].position.set(cameraConfig.x || 0, cameraConfig.y || 0, cameraConfig.z || 0);
				this.camera[materialF].lookAt(cameraConfig.orign_x || 0, cameraConfig.orign_y || 0, cameraConfig.orign_z ||
					0);
				if (materialF === 'mesh') {
					this.$refs.three.appendChild(this.renderer[materialF].domElement);
					// 立方体 材料 网格
					// MeshBasicMaterial不会受灯光影响更换MeshPhongMaterial
					const material = new Three.MeshPhongMaterial(materialConfig);
					const geometry = new Three.BoxGeometry();
					this.materials[materialF] = new Three.Mesh(geometry, material);
					this.scene[materialF].add(this.materials[materialF]);
					// 灯光
					const light = new Three.DirectionalLight('0xFFFFFF', 1)
					light.position.set(-1, 2, 4)
					this.scene[materialF].add(light)
					// 纹理
					const texture = new Three.TextureLoader().load( "../../static/pd.jpg" );
					texture.wrapS = Three.RepeatWrapping;
					texture.wrapT = Three.RepeatWrapping;
					texture.repeat.set( 4, 4 );
					this.materialF = materialF
					this.animate();
				} else if (materialF === 'line') {
					this.$refs.threeLine.appendChild(this.renderer[materialF].domElement);
					const points = []
					points.push(new Three.Vector3(-10, 0, 0))
					points.push(new Three.Vector3(0, 10, 0))
					points.push(new Three.Vector3(10, 0, 0))
					const material = new Three.LineBasicMaterial(materialConfig)
					const geometry = new Three.BufferGeometry().setFromPoints(points);
					this.materials[material] = new Three.Line(geometry, material)
					this.scene[materialF].add(this.materials[material]);
					this.renderer[materialF].render(this.scene[materialF], this.camera[materialF])
				}

			},
			animate() {
				let canvas = this.renderer[this.materialF].domElement
				// 检查canvas尺寸和显示尺寸---响应式（页面变化，监听canvas变化，重置图片尺寸缓冲区和相机长宽比）
				if(this.resizeRendererToDisplaySize(this.renderer[this.materialF], canvas)) {
					// canvas 不是铺满页面公用，防止页面拉伸
					this.camera[this.materialF].aspect = canvas.clientWidth / canvas.clientHeight
					this.camera[this.materialF].updateProjectionMatrix();
				}
				this.materials[this.materialF].rotation.x += 0.01;
				this.materials[this.materialF].rotation.y += 0.01;
				this.renderer[this.materialF].render(this.scene[this.materialF], this.camera[this.materialF])
				requestAnimationFrame(this.animate)
			},
			// 检查canvas尺寸和显示尺寸,设置缓冲区
			resizeRendererToDisplaySize(renderer, canvas) {
			  const width = canvas.clientWidth;
			  const height = canvas.clientHeight;
			  const needResize = canvas.width !== width || canvas.height !== height;
			  if (needResize) {
			    renderer.setSize(width, height, false);
			  }
			  return needResize;
			}
		},
		mounted() {
			console.log(window)
			this.initThree('mesh', {
				angle: 75,
				w_to_h: window.innerWidth / window.innerHeight * 2,
				near: 0.1,
				far: 1000,
				z: 2
			}, {
				width: window.innerWidth,
				height: window.innerHeight / 2
			}, {
				color: 0x44aa88
			})
			this.initThree('line', {
				angle: 45,
				w_to_h: window.innerWidth / window.innerHeight * 2,
				near: 1,
				far: 500,
				z: 100
			}, {
				width: window.innerWidth,
				height: window.innerHeight / 2
			}, {
				color: 0x0000ff
			})
		}
	}
</script>

